<template>
  <view>
    <view id="myMapView" style="height: 623px;"></view>

    <view class="controls">
      <input type="text" v-model="destination" placeholder="请输入终点地址，如：北京市朝阳区" />
      <button @click="planRoute">规划路径</button>
    </view>

    <view v-if="route">
      <text>路径规划结果：</text>
      <text>距离：{{ route.distance }} 米</text>
      <text>红绿灯个数：{{ route.traffic_lights }}</text>
      <view v-for="(step, index) in route.steps" :key="index">
        <text>路段{{ index + 1 }}：{{ step.road }}，距离：{{ step.distance }} 米</text>
      </view>
    </view>
  </view>
</template>

<script module="myMapViews" lang="renderjs">
  import { loadModules } from 'esri-loader';
  export default {
    name: 'myMapView',
    data() {
      return {};
    },
    mounted() {
      this.createMapView();
    },
    methods: {
      createMapView() {
        const options = {
          url: 'https://js.arcgis.com/4.14/init.js',
          css: 'https://js.arcgis.com/4.14/esri/themes/light/main.css'
        };
        loadModules([
          "esri/Map",
          "esri/views/MapView",
          "esri/Graphic",
          "esri/layers/GraphicsLayer",
          "esri/geometry/Polyline"
        ], options).then(([Map, MapView, Graphic, GraphicsLayer, Polyline]) => {
          this.map = new Map({
            basemap: "topo-vector"
          });
          this.view = new MapView({
            container: "myMapView",
            map: this.map,
            center: [126.60662,45.70525], // Beijing default center
            zoom: 13,
            logo: false // Hide watermark
          });
          this.Graphic = Graphic;
          this.Polyline = Polyline;
          this.graphicsLayer = new GraphicsLayer();
          this.map.add(this.graphicsLayer);
          this.watchPosition();
        });
      },
      addMarker(lon, lat) {
        const point = {
          type: "point",
          longitude: lon,
          latitude: lat
        };
        const markerSymbol = {
          type: "simple-marker",
          color: [226, 119, 40], // Orange
          outline: {
            color: [255, 255, 255], // White
            width: 2
          }
        };
        const pointGraphic = new this.Graphic({
          geometry: point,
          symbol: markerSymbol
        });
        this.graphicsLayer.add(pointGraphic);
        this.view.center = [lon, lat];
        this.view.zoom = 15;
      },
      drawRoute(path) {
        const polyline = new this.Polyline({
          paths: path,
          spatialReference: { wkid: 4326 }
        });
        const lineSymbol = {
          type: "simple-line",
          color: [226, 119, 40], // Orange
          width: 4
        };
        const polylineGraphic = new this.Graphic({
          geometry: polyline,
          symbol: lineSymbol
        });
        this.graphicsLayer.add(polylineGraphic);
      },
      watchPosition() {
        navigator.geolocation.watchPosition(
          (position) => {
            const { longitude, latitude } = position.coords;
            this.$parent.origin = `${longitude},${latitude}`;
            this.addMarker(longitude, latitude);
          },
          (err) => {
            console.error(err);
          },
          { enableHighAccuracy: true, maximumAge: 10000, timeout: 27000 }
        );
      }
    }
  }
</script>

<script>
export default {
  data() {
    return {
      origin: '',
      destination: '',
      route: null
    };
  },
  methods: {
    async planRoute() {
      if (!this.origin || !this.destination) {
        uni.showToast({
          title: '请输入终点',
          icon: 'none'
        });
        return;
      }

      try {
        const destinationCoords = await this.getGeocode(this.destination);
        const params = {
          origin: this.origin,
          destination: `${destinationCoords.lng},${destinationCoords.lat}`,
          strategy: 1,
          key: 'e728659ae370bdd007ad5ac5d9080cf5'
        };
        const data = await this.getRoutePlanning(params);
        if (data.route && data.route.paths && data.route.paths.length > 0) {
          this.route = data.route.paths[0];
          const path = this.route.steps.map(step => {
            return step.polyline.split(';').map(point => {
              const [lon, lat] = point.split(',');
              return [parseFloat(lon), parseFloat(lat)];
            });
          }).flat();
          this.$refs.myMapView.drawRoute(path);
        } else {
          uni.showToast({
            title: '未找到有效路径',
            icon: 'none'
          });
        }
      } catch (err) {
        uni.showToast({
          title: err.errmsg || '请求失败',
          icon: 'none'
        });
      }
    },
    getRoutePlanning(params) {
      return new Promise((resolve, reject) => {
        let url = 'https://restapi.amap.com/v3/direction/driving?';
        for (let key in params) {
          url += `${key}=${params[key]}&`;
        }
        url = url.slice(0, -1);

        uni.request({
          url: url,
          method: 'GET',
          success: (res) => {
            if (res.statusCode === 200 && res.data.infocode === "10000") {
              resolve(res.data);
            } else {
              reject(res.data);
            }
          },
          fail: (err) => {
            reject(err);
          }
        });
      });
    },
    getGeocode(address) {
      return new Promise((resolve, reject) => {
        let url = `https://restapi.amap.com/v3/geocode/geo?address=${address}&key=e728659ae370bdd007ad5ac5d9080cf5`;

        uni.request({
          url: url,
          method: 'GET',
          success: (res) => {
            if (res.statusCode === 200 && res.data.geocodes.length > 0) {
              const location = res.data.geocodes[0].location.split(',');
              resolve({ lng: parseFloat(location[0]), lat: parseFloat(location[1]) });
            } else {
              reject(res.data);
            }
          },
          fail: (err) => {
            reject(err);
          }
        });
      });
    }
  },
  mounted() {
    this.$refs.myMapView.watchPosition();
  }
};
</script>

